import styled from "styled-components";
import Servicebg from "../../assets/images/services-bg.jpg";

export const Board = styled.div`
  width: 100vw;
  height: 70vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(${Servicebg});
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  @media (max-width: 788px) {
    height: 55vh;
  }
  @media (max-width: 512px) {
    height: 45vh;
  }
  &:hover {
    .cover {
      margin-top: 0;
    }
    h3 {
      color: #ddd;
      left: 2.2vw;
    }
  }
  .cover {
    background-color: rgba(0, 0, 0, 0.5);
    width: 100vw;
    height: 70vh;
    display: flex;
    align-items: center;
    margin-top: -138vh;
    transition-duration: 0.4s;
    position: relative;
  }
  h3 {
    position: absolute;
    top: 33vh;
    left: 52vw;
    font-size: 5.5vw;
    color: #ea1c3a;
    color: #ccc;
    transition-duration: 0.6s;
    @media (max-width: 788px) {
      font-size: 6.5vw;
    }
    @media (max-width: 512px) {
      font-size: 7vw;
    }
  }
`;

export const Servicestyle = styled.div`
  width: 100vw;
  ul {
    font-size: 2.1vw;
    list-style: none;
    @media (max-width: 788px) {
      font-size: 3.6vw;
    }
    @media (max-width: 512px) {
      font-size: 4.8vw;
    }
    li {
      line-height: 1.7vw;
      font-size: 1.3vw;
      margin: 0.6vh 0;
      @media (max-width: 788px) {
        font-size: 2.7vw;
        line-height: 3.9vh;
        margin: 1.2vh 0;
      }
      @media (max-width: 512px) {
        font-size: 3.9vw;
        line-height: 4.6vh;
        margin: 1.4vh 0;
      }
      img {
        width: 1.3vw;
        @media (max-width: 788px) {
          width: 2.2vw;
        }
        @media (max-width: 512px) {
          width: 3.3vw;
        }
      }
    }
`;
