import React from "react";
import { Servicestyle, Board } from "./style";
import flame from "../../assets/images/flames.png";

function service() {
  return (
    <>
      <Board>
        <div className="cover"></div>
        <h3>Услуги</h3>
      </Board>
      <Servicestyle>
        <ul>
          Наши услуги
          <li>
            <img src={flame} alt="" />
            Проектирование систем противопожарной автоматики.
          </li>
          <li>
            <img src={flame} alt="" />
            Проектное сопровождение при проведении строительных работ.
          </li>
          <li>
            <img src={flame} alt="" />
            Противопожарный аудит объекта ( соответствие объекта требованиям норм пожарной безопасности ).
          </li>
          <li>
            <img src={flame} alt="" />
            
          </li>
          
        </ul>
      </Servicestyle>
    </>
  );
}

export default service;
